import React from 'react'
import Card from "./Card"
import data from "./../cards.json";
const Cards = () => {
  return (
    <>
      {
        data.map(({lang,url,fcolor,scolor},i) => {
          return <Card lang={lang} src={url} fcolor={fcolor} scolor={scolor} key={i} />
        })
      }
    </>
  )
}

export default Cards

